.app{

    height:100vh;
    width:100%;
    display:flex;
    flex-direction: column;
}

.app_navbar{

    width:100%;
    padding: 10px 20px;
    text-align: center;
    background-color: rgb(241 195 128);
    border: 2px;
    border-radius: 10px;
}

.app_displayBar{

    width:100%;
    padding:5px 20px;
    background-color:rgb(228 195 244);
    border:2px;
    border-radius: 10px;



}

.app_outer{

    flex: 1;
    width: 100%;
    overflow-x: auto;
    
}

.app_boards{

    padding: 20px;
    padding-bottom: 5px;;
    height: 100%;
    min-width: fit-content;
    display: flex;
    gap: 30px;


}