.card{

    padding: 10px;
    border-radius: 15px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.card_footer{
    display: flex;
    gap: 5px;
    justify-content: space-between;
    align-items: center;

}

.card_footer svg{
    height: 12px;
    width: 12px;
}

.card_title{
    font-weight: bold;
}

.card_top_title{
    display: flex;
    justify-content: space-between;
}

.card_footer_labels{

    flex:1;
    display: flex;
    gap: 10px;
    justify-content: space-between;

}

