.board{

    height: 100%;
    min-width: 290px;
    max-height: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: beige;
    border-radius: 10px;

}

.board_top{
    display: flex;

}

.board_top_title{
    flex: 1;
    display: flex;
    align-items: center;
    font-weight: bold;
}

.board_top_title span{
    color:gray;
}

.board_cards{

    flex: 1;
    height: 100%;
    background-color: azure;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
    border-bottom: 10px;
    overflow-y: auto;



}